module.exports = "<div class=markdown><h1 id=form>Form</h1> <hr> <p>具有数据收集、校验和提交功能的表单，包含复选框、单选框、输入框、下拉选择框等元素。</p> <h2 id=->表单</h2> <p>我们为 form 提供了以下两种排列方式：</p> <ul> <li>水平排列：标签和表单控件水平排列；（默认）</li> <li>垂直排列：标签和表单控件上下垂直排列；</li> <li>行内排列：表单项水平行内排列。</li> </ul> <h2 id=->表单域</h2> <p>表单一定会包含表单域，表单域可以是输入控件，标准表单域，标签，下拉菜单，文本域等。这里我们封装了表单域<code>&lt;v-form-item&gt;&lt;/v-form-item&gt;</code></p> <pre><code class=lang-html>&lt;v-form-item {...props}&gt;\n  {slot}\n&lt;/v-form-item&gt;\n</code></pre> <h2 id=api>API</h2> <p>Form</p> <table> <thead> <tr> <th>属性</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>type</td> <td>表单布局，可选值为 <code>inline</code> <code>horizontal</code> 或者不设</td> <td>String</td> <td>undefined</td> </tr> </tbody> </table> <p>Form-item</p> <table> <thead> <tr> <th>属性</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>title</td> <td>label 标签的文本</td> <td>String</td> <td>undefined</td> </tr> <tr> <td>required</td> <td>是否必填</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>waiting</td> <td>手动控制输入框是否为加载状态</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>wrong</td> <td>手动控制输入框是否为错误状态</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>warning</td> <td>手动控制输入框是否为警告状态</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>success</td> <td>手动控制输入框是否为成功状态</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>content</td> <td>输入框备注</td> <td>Boolean</td> <td>false</td> </tr> </tbody> </table> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>基本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-form> <v-form-item title=\"Email address\"> <v-input placeholder=Email></v-input> </v-form-item> <v-form-item title=Password> <v-input placeholder=Password html-type=password></v-input> </v-form-item> <v-form-item> <v-checkbox>Remember me</v-checkbox> </v-form-item> <v-form-item> <v-button type=default html-type=submit>Submit</v-button> </v-form-item> </v-form> </div> <div class=\"code-boxes-col-2-1 code-box-code\"