ctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Submit<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-button</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-form-item</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-form</span><span class=\"token punctuation\">></span></span>\n</pre></div> </div> <div class=code-box> <h3>内联表单</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-form type=inline> <v-form-item> <v-input placeholder=Email icon=user></v-input> </v-form-item> <v-form-item> <v-input placeholder=Password html-type=password icon=lock></v-input> </v-form-item> <v-form-item> <v-button type=primary html-type=submit>Login</v-button> </v-form-item> </v-form> <br/> <v-form type=inline> <v-form-item> <v-input :group=true addon-before=$ addon-after=.00 addon=addon html-type=number></v-input> </v-form-item> <v-form-item> <v-button type=primary html-type=submit>Submit</v-button> </v-form-item> </v-form> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-form</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>inline<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-form-item</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-input</span> <span class=\"token attr-name\">placeholder</span><span 