module.exports = "<div class=markdown><h1 id=transfer>Transfer</h1> <hr> <p>双栏穿梭选择框。</p> <h2 id=->何时使用</h2> <p>用直观的方式在两栏中移动元素，完成选择行为。</p> <p>选择一个或以上的选项后，点击对应的方向键，可以把选中的选项移动到另一栏。 其中，左边一栏为 source，右边一栏为 target，API 的设计也反应了这两个概念。</p> <h2 id=->如何使用</h2> <pre><code class=lang-html>data () {\n  return {\n    titles: [&#39;Source&#39;, &#39;Target&#39;],\n    operations: [&#39;To Left&#39;, &#39;To Right&#39;],\n    datas: [],\n    targets: []\n  }\n},\ncreated () {\n  var self = this\n  const mockData = []\n  for (let i = 0; i &lt; 20; i++) {\n    mockData.push({\n      key: i,\n      title: `content${i + 1}`,\n      description: `description of content${i + 1}`,\n      disabled: i % 3 &lt; 1\n    })\n  }\n  self.datas = mockData\n  const targetKeys = mockData\n    .filter(item =&gt; +item.key % 3 &gt; 1)\n    .map(item =&gt; item.key)\n  self.targets = targetKeys\n}\n</code></pre> <h2 id=api>API</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>titles</td> <td>标题集合,顺序从左至右</td> <td>Array</td> <td>undefined</td> </tr> <tr> <td>operations</td> <td>操作文案集合,顺序从上至下</td> <td>Array</td> <td>undefined</td> </tr> <tr> <td>dataSource</td> <td>数据源，其中的数据将会被渲染到左边一栏中，<code>targetKeys</code>中指定的除外。</td> <td>Array</td> <td>[]</td> </tr> <tr> <td>targetKeys</td> <td>显示在右侧框数据的key集合</td> <td>Array</td> <td>[]</td> </tr> <tr> <td>onchange</td> <td>选项在两栏之间转移时的回调函数</td> <td>Function</td> <td>() =&gt; {}</td> </tr> <tr> <td>showFilter</td> <td>是否显示搜索框</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>showDescription</td> <td>是否显示备注</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>showReload</td> <td>是否显示底部，底部含<code>clear all</code>和<code>Reload</code>按钮用于重置数据</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>searchPlaceholder</td> <td>搜索框的默认值</td> <td>String</td> <td>&#39;Search&#39;</td> </tr> <tr> <td>notFoundContent</td> <td>当列表为空时显示的内容</td> <td>String</td> <td>&#39;Not Result&#39;</td> </tr> </tbody> </table> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>基本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-transfer :titles=titles :data-source=datas :target-keys=targets></v-transfer> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-transfer</span> <span class=\"token attr-name\">:titles</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>titles<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:data-source</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>datas<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:target-keys</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>targets<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-transfer</span><span class=\"token punctuation\">></span></span>\n</pre></div> </div> <div class=code-box> <h3>带搜索框</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-transfer :titles=titles :data-source=datas :target-keys=targets :show-filter=true></v-transfer> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-transfer</span> <span class=\"token attr-name\">:titles</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>titles<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:data-source</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punct