module.exports = "<div class=markdown><h1 id=steps>Steps</h1> <hr> <p>引导用户按照流程完成任务的导航条。</p> <h2 id=->何时使用</h2> <p>当任务复杂或者存在先后关系时，将其分解成一系列步骤，从而简化任务。</p> <h2 id=api>API</h2> <h3 id=steps>Steps</h3> <p>整体步骤条。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>current</td> <td>可选参数，指定当前处理正在执行状态的步骤，从0开始记数。在子Step元素中，可以通过status属性覆盖状态。</td> <td>number</td> <td>无</td> <td>0</td> </tr> <tr> <td>size</td> <td>可选参数，指定大小（目前只支持普通和迷你两种大小）。</td> <td>string</td> <td>small, default</td> <td>default</td> </tr> <tr> <td>direction</td> <td>可选参数，指定步骤条方向（目前支持水平和竖直两种方向，默认水平方向）。</td> <td>string</td> <td>vertical</td> <td>无</td> </tr> <tr> <td>maxDescriptionWidth</td> <td>可选参数，指定步骤的详细描述文字的最大宽度。</td> <td>number</td> <td>无</td> <td>100</td> </tr> </tbody> </table> <h3 id=steps-step>Steps.Step</h3> <p>步骤条内的每一个步。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>status</td> <td>可选参数，指定状态。当不配置该属性时，会使用父Steps元素的current来自动指定状态。</td> <td>string</td> <td>wait, process, finish</td> <td>wait</td> </tr> <tr> <td>title</td> <td>必要参数，标题。</td> <td>string/jsx</td> <td>无</td> <td>无</td> </tr> <tr> <td>description</td> <td>可选参数，步骤的详情描述。</td> <td>string/jsx</td> <td>无</td> <td>空</td> </tr> <tr> <td>icon</td> <td>可选参数，步骤的Icon。如果不指定，则使用默认的样式。</td> <td>string/jsx</td> <td>无</td> <td>空</td> </tr> </tbody> </table> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>简单的步骤条</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-steps :current=1> <v-step title=已完成 description=这里是多信息的描述啊></v-step> <v-step title=进行中 description=这里是多信息的耶哦耶哦哦耶哦耶></v-step> <v-step title=有一个待运行 description=描述啊描述啊></v-step> <v-step title=待运行 description=这里是多信息的描述啊></v-step> </v-steps> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-steps</span> <span class=\"token attr-name\">:current</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>1<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-step</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>已完成<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">description</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>这里是多信息的描述啊<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-step</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-step</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>进行中<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">description</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>这里是多信息的耶哦耶哦哦耶哦耶<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-step</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-step</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</